	module.exports = {"data":{"post":{"id":"/Users/georgiabarrie/gatsby-afc-guide/content/posts/2017-12-03--increasing-milk-production-part-2/index.md absPath of file >>> MarkdownRemark","html":"<blockquote style=\"background: #f2f3f4;\">\n<p style=\"text-align: center; font-weight: 300; font-size: 0.9em\">To add your question press <img style=\"height:2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/comment_icon.png\">  in bottom corner &#x1F447;</p>\n</blockquote>\n<div style=\"text-align: center\">\n<a href=\"/about\"><img style=\"width: 100%;\" src=\"https://s3.amazonaws.com/afc-dairytrial/key_bar-1.png\"></a>\n</div>\n<br><br>\nThis is Part 2 of our guide on how to increase your milk production. If you haven&apos;t already read Part 1 then we recommend you start here:\n<ul>\n<li><a href=\"/increasing-milk-production-part-1\">Increasing milk production - Part 1</a>\n<br></br></li>\n</ul>\n<blockquote style=\"background: #D7F19F;\">\n<h3>Reason 4 - NOT ENOUGH MINERALS</h3>\n<p>Does your dairy cow’s skin look shiny and healthy? If not then your cow may be lacking essential minerals.\n<br></br>\nYou can buy granular mineral salts from any agrovet and mix them in with the feeds. Dr Muchibi from Elgon Kenya suggests: “Provide mineral salts at a rate of 150g for every 5 litres of milk produced, and an extra 60g for every 5 extra litres.”\n<br></br>\nYou can also buy mineral licks which you should make easily accessible so your cow can use the lick whenever she wants.\n<br><br></p>\n<div>\n    Source:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/ILRI-logo.png\">\n    <b>ILRI</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>21</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<h3>Top Q&#x26;A from AFC members</h3>\n<p>Joseph asked a key question regarding minerals…</p>\n<blockquote style=\"background: #6EA6A2;\">\n<h3>Joseph’s Question</h3>\n<p>  Helloooo farmers… Some one gave me this idea of giving mineral lick adlib to my five dairy cows instead of using the 100-200grm measure per cow per day… The argument is each cow will lick what it feels is enough just like they drink water… Need some professional advice… </p>\n  <br>\n<div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_production2_joseph.jpg\">\n    <b>John N</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>12</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<p>Patrick explains the advantages and disadvantages of the different mineral feeding methods…</p>\n<blockquote style=\"background: #96e58e;\">\n<h3>Patrick’s Answer</h3>\n<p>Minerals and vitamins has two approach while feeding and both has its own advantage and disadvantage.\n<br><br>\nOne is adlib feeding - its most ideal but faces more challenges:</p>\n<ol>\n<li>If minerals are not palatable cows will experience deficiencies and more wastages hence loss to a farmer.</li>\n<li>Some of raw materials used are highly soluble thus when come into contact with saliva or water they are lost.</li>\n<li>Due to the pecking order some cows might not be able to access minerals.</li>\n<li>If its highly palatable within short duration its over hence more cost to farmer and advantageous to cows.</li>\n</ol>\n<p>Force feeding/regulated feeding - this one a farmer is sure the quantity offered and cows have consumed it but challenge is:</p>\n<ol>\n<li>Will cow requirement be met? if no then deficiencies.</li>\n<li>Is manufacturer quoted measure per cow per day honest with it or is there to please farmer on economic sense? If no then expect deficiencies.</li>\n<li>On farmer weighing how is the accuracy rate?</li>\n</ol>\n<p>My point of view that I opt for, definitely its open to factual critiques is:</p>\n<ol>\n<li>Have a mineral block</li>\n<li>on average a dairy cow needs 5-6kgs of minerals per month. Have 2kgs (40%) adlib and 3kgs (60%) regulated feeding.</li>\n</ol>\n<p>Based on cows need advice from your dairy expert is ideal. Manufacturer advice is also good to adhere to since they know raw materials used.</p>\n<br>\n  <div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_production2_patrick.jpg\">\n    <b>Patrick N</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>44</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<p>There may be a less obvious reason for low milk production and that is stress…</p>\n<blockquote style=\"background: #D7F19F;\">\n<h3>Reason 4 - STRESS</h3>\n<p>Many farmers report that cows that are calm, happy and comfortable produce more milk. Providing a more comfortable and stress-free environment is a very cheap way of increasing your milk production.</p>\n<p>There are 3 main factors that might be causing your dairy cows stress.</p>\n<ol>\n<li>Heat or cold stress – caused by very hot or cold weather</li>\n<li>Environmental stress – caused by uncomfortable housing, poor cleaning of the cow shed or cows being transported</li>\n<li>Psychological stress – remember that to produce a good amount of milk cows need to feel safe and calm. When you milk a cow you need to be gentle and soothing. Treating a cow roughly, lots of loud noises or being around other stressed animals will reduce your milk production.</li>\n</ol>\n<br>\n<div>\n    Source:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/ILRI-logo.png\">\n    <b>ILRI</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>21</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<h3>Real Farmer Stories</h3>\n<p>Check out David’s experience with increasing milk production by making his cows more comfortable…</p>\n<blockquote style=\"background: #96e58e;\">\n<div>\n  <img style=\"width: 100%\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_production2_davidstory.png\">\n  </div>\n  <br>\n  \"The key to continuous improvement of your dairy farm is regular observation of your herd and attention to any slight discomfort you come across. For instance in our farm, we observed that the hard surface at the milking parlor was making the cows very restless and at times they'd kick off the milking equipment. We decided to put a mat for them to be stepping on and the results are amazing. You might as well go for your cup of tea as you patiently wait for your milk\"\n<br>\n  <a href=\"https://www.facebook.com/groups/DairyFarmingKenya/permalink/1363109630418648/\">\n    <img style=\"height:2em; border-radius: 2em;border: 1px solid #ffff;\" src=\"https://s3.amazonaws.com/afc-dairytrial/read_full_story.png\">\n  </a>\n  <br><br>\n<div>\n    Posted by:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/afc_dairy_guide_production2_david.png\">\n    <b>David G</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>49</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<blockquote style=\"background: #D7F19F;\">\n<h3>Reason 5 - OTHER ISSUES</h3>\n<p>A sudden drop in milk production could be a sign that your cow is unwell. If you see a sudden change in milk production and you have not changed your feeding or milking process then we recommend you get your cow checked with a vet asap.</p>\n<br>\n<div>\n    Source:\n    <img style=\"height:3.2em; border-radius: 2em; border: 1px solid #ffff\" src=\"https://s3.amazonaws.com/afc-dairytrial/ILRI-logo.png\">\n    <b>ILRI</b>\n    <div style=\"float: right;margin-top:0px;\"> <b>21</b>\n      <a href=\"#\">\n        <img style=\"height:3.2em; border-radius: 2em;\" src=\"https://s3.amazonaws.com/afc-dairytrial/clap_btn.jpg\">\n      </a>\n    </div>\n  </div>\n</blockquote>\n<hr>\n<h3>Read more topics</h3>\n<p>There are number of other important topics in this guide:</p>\n<ul>\n<li><a href=\"/increasing-milk-production-part-1\">Increasing milk production - Part 1</a> ⭐ - Part 1 on increasing production</li>\n<li><a href=\"/preventing-and-treating-milk-fever\">Preventing and treating milk fever</a> ⭐ - Guidance on milk fever issues</li>\n<li><a href=\"/feeding-your-cow-basics\">Feeding your cows - basics</a>  ⭐- Understand the basics of feeding</li>\n</ul>\n<p>…or you can just <a href=\"/\">see all topics</a></p>","fields":{"slug":"/increasing-milk-production-part-2/","prefix":"2017-12-03"},"frontmatter":{"title":"Increasing  Milk Production - part 2","subTitle":"Production","cover":{"childImageSharp":{"resize":{"src":"/static/unlock-increasing-milk-production-5baf116b2a7e18252c696a2366ad9890-160fa.png"}}}}},"author":{"id":"/Users/georgiabarrie/gatsby-afc-guide/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Africa Farmers Club</strong> has a mission to bring African farmers together!</p>"},"footnote":{"id":"/Users/georgiabarrie/gatsby-afc-guide/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<p>Made with ♥️ by Africa Farmers Club</p>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/increasing-milk-production-part-2/"}}

